import { NgModule, InjectionToken } from '@angular/core';
import { AuthComponent } from './auth-lib.component';

export interface AuthConfig {
  authenticationUrl: string;
  refreshUrl: string;
  redirectUrl: string;
  defaultType: string;
}

export interface AuthTypes {
  KEYCLOAK: string;
  SIGNICAT: string;
  VEPA: string;
  CAPTCHA: string;
}

export interface AccessData {
  accessToken?: string;
  refreshToken?: string;
  redirect?: string;
}

export const DCS_AUTH_CONFIG = new InjectionToken<AuthConfig>('Authentication Configuration');

@NgModule({
  declarations: [AuthComponent],
  imports: [],
  providers: [
    {
      provide: DCS_AUTH_CONFIG,
      useValue: { authenticationUrl: 'authenticate', refreshUrl: 'refresh', redirectUrl: 'redirect', defaultType: 'keycloak' }
    }
  ],
  exports: [AuthComponent]
})
export class AuthModule { }
